::-webkit-scrollbar {
	width: 8px; /* Mostly for vertical scrollbars */
	height: 8px; /* Mostly for horizontal scrollbars */
}

::-webkit-scrollbar-track {
	/* Foreground */
	background-color: map-get($color-grey, light-4);
	box-shadow: map-get($box-shadow, lg);
}

::-webkit-scrollbar-thumb {
	/* Background */
	// margin: 0 map-get($space, s4);
	box-shadow: map-get($box-shadow, main);
	background-color: map-get($color-primary, main);
	border-radius: 10px;
	transition: all map-get($transition-duration, main);
}

::-webkit-scrollbar-thumb:hover {
	background-color: map-get($color-primary, dark-1);
}

::-webkit-scrollbar-corner {
	background-color: map-get($color-primary, light-1);
}
