/*
**  define the style for the body in the page
*/
body {
	font-family: 'Rubik', sans-serif;
	font-size: $default-font-size;
	font-weight: map-get($font-weight, main);
	line-height: map-get($line-height, xs);
	color: map-get($color-grey, main);
}

.heading-primary,
.heading-secondary {
	font-weight: map-get($font-weight, xxl);
	color: map-get($color-grey, dark-1);
	letter-spacing: map-get($letter-spacing, sm);
	line-height: map-get($line-height, sm);
}

/*
**  define the style for the primary heading in the page  =>heading-primary
*/
.heading-primary {
	font-size: 4.4rem;
	margin-bottom: 9.6rem;
	@include rwd-df(xl) {
		font-size: 3.6rem;
	}

	@include rwd-df(md) {
		margin-bottom: 6.4rem;
	}

	@include rwd-df(sm) {
		text-align: center;
		font-size: 3rem;
		margin-bottom: 4.8rem;
	}
}

/*
**  define the style for the secondary heading in the page  =>heading-secondary
*/
.heading-secondary {
	font-size: 3rem;
	margin-bottom: 3.2rem;

	@include rwd-df(xl) {
		font-size: 2.4rem;
	}

	@include rwd-df(sm) {
		font-size: 2.4rem;
	}
}

/*
**  define the style for the sub heading in the page  => sub-heading
*/
.sub-heading {
	display: block;
	font-size: 1.6rem;
	margin-bottom: 1.6rem;
	font-weight: map-get($font-weight, lg);
	text-transform: uppercase;
	color: map-get($color-primary, dark-1);
	letter-spacing: map-get($letter-spacing, g);

	@include rwd-df(sm) {
		text-align: center;
	}
}

// /*
// **  define the style for the paragraph  in the page
// */
.paragraph {
	font-size: 2rem;
	line-height: map-get($line-height, main);

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
}

/*
**  define the style for the sections  in the page
*/
.sec {
	padding: map-get($space, l3) 0;

	@include rwd-df(sm) {
		padding: map-get($space, l1) 0;
	}
}
