.meal-card {
	box-shadow: map-get($box-shadow, main);
	border-radius: map-get($border-radius, lg);
	overflow: hidden;
	transition: all map-get($transition-duration, slow);

	&:hover {
		transform: translateY(-1.2rem);
		box-shadow: 0 3.2rem 6.4rem rgb(0 0 0 / 6%);
	}

	&__img {
	}

	&__body {
		padding: 3.2rem;

		@include rwd-df(lg) {
			padding: 3rem;
		}

		@include rwd-df(md) {
			padding: 2.4rem;
		}

		@include rwd-df(xs) {
			padding: 3.2rem;
		}
	}

	&__tags {
		display: flex;
		gap: 0.4rem;
		margin-bottom: 1.2rem;
	}

	&__tag {
		display: inline-block;
		font-size: 1.2rem;
		padding: 0.4rem 0.8rem;
		text-transform: uppercase;
		color: #333;
		border-radius: 100px;
		font-weight: 600;
		// background-color: #fff;

		&--vegan {
			background-color: map-get($color-secondary, main);
		}

		&--paleo {
			background-color: map-get($color-tertiary, main);
		}
	}

	&__title {
		font-size: 2.4rem;
		color: map-get($color-grey, dark-1);
		font-weight: map-get($font-weight, xl);
		margin-bottom: 3.2rem;
	}

	&__features {
		display: flex;
		flex-direction: column;
		gap: 2rem;

		&__item {
			display: flex;
			align-items: center;
			gap: 1.5rem;
			&__icon {
				@include squareSize(2.4rem);
				fill: map-get($color-primary, main);
				// fill: transparent;
			}

			&__text {
			}
		}
	}
}
