.social-list {
	display: flex;
	gap: 3rem;
	&__item {
		&__link {
			text-decoration: none;
			display: block;
			&__icon {
				@include squareSize(3rem);
				// fill: map-get($color-grey, light-2);
				fill: map-get($color-grey, light-3);
				cursor: pointer;
				transition: all map-get($transition-duration, main);

				&:hover {
					// fill: map-get($color-grey, main);
					fill: map-get($color-primary, main);
				}
			}
		}
	}
}
