.scroll-to-top-btn {
	position: fixed;
	bottom: 5rem;
	#{$end}: 3rem;
	padding: 1rem;
	background-color: map-get($color-primary, dark-1);
	color: map-get($color-primary, light-3);
	@include squareSize(4rem);
	font-size: 2rem;
	border-radius: map-get($border-radius, main);
	font-weight: map-get($font-weight, xxl);
	line-height: 0;
	display: flex;
	justify-content: center;
	align-items: center;

	display: block;
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	transform: scale(0);
	transition: all map-get($transition-duration, lg);
	border: 0.2rem solid transparent;

	&.show {
		visibility: visible;
		opacity: 1;
		transform: scale(1);
		pointer-events: auto;

		&:hover {
			background-color: map-get($color-primary, light-3);
			color: map-get($color-primary, dark-1);
			border-color: map-get($color-primary, dark-1);
			border-radius: 0;
		}
	}
}
