.sec-hero {
	background-color: map-get($color-primary, light-3);
	// padding: map-get($space, l1) 0;
	// min-height: 100vh;

	background-image: linear-gradient(
			rgba(34, 34, 34, 0.7),
			rgba(34, 34, 34, 0.7)
		),
		url('../../assets/img/hero/hero.jpg');

	@include backgroundImage;
	background-position: 0% 0%;

	@include rwd-df(lg) {
		background-position: right center;
	}

	@include rwd-df(sm) {
		background-position: 55% center;
	}

	&__content {
		min-height: 100vh;
		display: flex;
		align-items: center;
		width: 50%;
		// margin: 0 auto;

		@include rwd-df(lg) {
			margin: 0 auto;
			width: 75%;
			text-align: center;
		}

		@include rwd-df(sm) {
			width: 100%;
		}
	}

	&__text {
		color: map-get($color-primary, light-3);
		// color: $color-white;
		@include rwd-df(md) {
			// text-align: center;
		}
		&__title {
			font-size: 4.4rem;
			margin-bottom: 3.2rem;

			line-height: map-get($line-height, xs);
			font-weight: map-get($font-weight, xxl);
			color: inherit;
			// color: map-get($color-grey, dark-1);
			letter-spacing: map-get($letter-spacing, sm);
		}
		&__description {
			font-size: 2rem;
			color: inherit;
			line-height: map-get($line-height, main);
			margin-bottom: 4.8rem;
		}

		&__btns {
			display: flex;
			gap: 2rem;
			@include rwd-df(lg) {
				justify-content: center;
			}

			@include rwd-df(xs) {
				gap: 1rem;
			}
		}
	}

	.delivered-meals {
		display: flex;
		align-items: center;
		margin-top: 8rem;
		gap: 4rem;
		@include rwd-df(lg) {
			justify-content: center;
			gap: 4rem;
			margin-top: 5rem;
		}

		@include rwd-df(sm) {
			gap: 3rem;
		}

		@include rwd-df(xs) {
			flex-direction: column;
			gap: 1rem;
		}

		&__imgs {
			display: flex;
			img {
				@include squareSize(4.8rem);
				border-radius: map-get($border-radius, circle);
				border: 3px solid map-get($color-primary, light-3);
				margin-right: -1.6rem;
			}
		}
		&__text {
			font-size: 1.8rem;
			font-weight: map-get($font-weight, xl);

			&__number {
				color: $color-white;
				// color: map-get($color-primary, dark-1);
				font-weight: map-get($font-weight, xxl);
			}
		}
	}
}
