.sec-how {
	&__steps {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		align-items: center;
		column-gap: 6.4rem;
		row-gap: 9.6rem;

		@include rwd-df(lg) {
			column-gap: 3.2rem;
		}

		@include rwd-df(sm) {
			grid-template-columns: 1fr;
			row-gap: 4.8rem;
		}

		&__text {
			.step__number {
				display: block;
				font-size: 8.6rem;

				@include rwd-df(sm) {
					font-size: 5.2rem;
				}

				font-weight: map-get($font-weight, xl);
				color: #ddd;
				margin-bottom: 1.2rem;
			}

			.step__description {
				font-size: 1.8rem;
				line-height: map-get($line-height, lg);
			}
		}
		&__image {
			&--1 {
				@include rwd-df(sm) {
					grid-row: 1;
				}
			}
			&--3 {
				@include rwd-df(sm) {
					grid-row: 5;
				}
			}

			position: relative;

			&::before,
			&::after {
				content: '';
				@include absCenter;

				border-radius: 50%;
				z-index: -1;
			}

			&::before {
				width: 60%;
				padding-bottom: 60%;
				background-color: map-get($color-primary, light-3);
			}

			&::after {
				width: 45%;
				padding-bottom: 45%;
				background-color: map-get($color-primary, light-2);
			}

			img {
				width: 35%;
				margin: 0 auto;
			}
		}
	}
}
