/*
**  define styling for element component with specified with classe  main header.
*/
.header {
	// background-color: map-get($color-primary, light-3);
	padding: 2.4rem 4.8rem;

	@include rwd-df(lg) {
		padding: 2.4rem 3.2rem;
	}

	position: absolute;
	top: 0;
	left: 0;
	width: 100%;

	&.sticky {
		position: fixed;
		// background-color: rgba($color-white, 0.97);
		background-color: rgba(map-get($color-grey, dark-1), 0.95);

		z-index: 1000;
		box-shadow: 0 1.2rem 3.2rem rgba($color-black, 0.03);
	}

	&__content {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 5rem;
	}

	&__logo {
		text-decoration: none;
		&__image {
			height: 2.2rem;
		}
	}

	.navbar {
		@include rwd-df(md) {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: rgba(map-get($color-grey, dark-1), 0.9);
			// background-color: rgba($color-white, 0.9);
			backdrop-filter: blur(5px);
			display: flex;
			justify-content: center;
			align-items: center;
			transform: translateX(100%);
			transition: all map-get($transition-duration, lg);
		}

		&__list {
			display: flex;
			align-items: center;
			gap: 3rem;

			@include rwd-df(md) {
				flex-direction: column;
			}

			&__item {
				&__link {
					&:link,
					&:visited {
						text-decoration: none;
						color: map-get($color-primary, light-3);
						// color: map-get($color-grey, dark-1);
						font-weight: map-get($font-weight, lg);
						font-size: 1.8rem;
						line-height: map-get($line-height, lg);
						transition: all map-get($transition-duration, main);

						@include rwd-df(md) {
							font-size: 3rem;
						}
					}

					&:hover,
					&:active {
						color: map-get($color-primary, dark-1);
					}

					&--cta-btn {
						&:link,
						&:visited {
							padding: 1.2rem 2.4rem;
							border-radius: map-get($border-radius, main);
							color: $color-white;
							background-color: map-get($color-primary, main);
						}

						&:hover,
						&:active {
							background-color: map-get($color-primary, dark-1);
						}
					}
				}
			}
		}
	}

	&__content.open .navbar {
		@include rwd-df(md) {
			transform: translateX(0);
		}
	}

	.toggler-btn {
		$bar-height: 0.4rem;
		$bar-transition: 0.5s;
		$bar-color: map-get($color-primary, light-3);
		// $bar-color: map-get($color-grey, main);
		display: none;

		@include rwd-df(md) {
			display: block;
		}

		position: relative;
		width: 3.5rem;
		height: 2.5rem;
		border: none;

		&__bar {
			position: absolute;
			display: block;
			width: 100%;
			height: $bar-height;
			background-color: $bar-color;
			#{$start}: 0px;
			border-radius: 1rem;

			transition: top $bar-transition $bar-transition,
				opacity $bar-transition $bar-transition, transform $bar-transition;

			&:nth-child(1) {
				top: 0;
			}
			&:nth-child(2) {
				top: calc(50% - ($bar-height / 2));
			}
			&:nth-child(3) {
				top: calc(100% - $bar-height);
			}
		}
	}

	&__content.open .toggler-btn {
		$bar-height: 0.4rem;
		$bar-transition: 0.5s;
		$bar-color: map-get($color-grey, main);

		&__bar {
			transition: top $bar-transition, opacity $bar-transition,
				transform $bar-transition $bar-transition;

			&:nth-child(1) {
				top: calc(50% - ($bar-height / 2));
				transform: rotate(45deg);
			}
			&:nth-child(2) {
				opacity: 0;
			}
			&:nth-child(3) {
				top: calc(50% - ($bar-height / 2));
				transform: rotate(-45deg);
			}
		}
	}
}
