.gallery {
	&__item {
		border-radius: map-get($border-radius, main);
		overflow: hidden;
		position: relative;

		&__image {
			transition: all map-get($transition-duration, slow);
		}

		&:hover &__image {
			transform: scale(1.2);
			filter: grayscale(90%);
		}

		&__link {
			@include absCenter;
			display: inline-block;
			svg {
				transform: scale(0);
				fill: map-get($color-primary, main);
				@include squareSize(6rem);
				transition: all map-get($transition-duration, slow);
			}
		}

		&:hover &__link svg {
			transform: scale(1);
		}
	}
}
