.pricing-plan {
	border: 2px solid rgb(253, 242, 233);
	padding: 4.6rem;
	width: 75%;
	border-radius: map-get($border-radius, lg);

	@include rwd-df(md) {
		width: 100%;
	}

	@include rwd-df(sm) {
		width: 75%;
	}

	&--starter {
	}

	&--complete {
		position: relative;
		overflow: hidden;
		background-color: map-get($color-primary, light-3);

		&::after {
			content: 'Best value';
			display: block;
			width: 100%;
			text-align: center;
			position: absolute;
			top: 8%;
			left: 88%;
			transform: translateX(-50%) rotate(45deg);

			text-transform: uppercase;
			font-size: 1.4rem;
			font-weight: map-get($font-weight, xxl);
			color: map-get($color-grey, dark-1);
			background-color: map-get($color-tertiary, main);
			padding: 0.7rem 0;
			// @include rwd-df(sm) {
			// 	padding: 0.8rem 10rem;
			// }

			// @include rwd-df(xs) {
			// 	padding: 0.8rem 8rem;
			// }
		}
	}

	&__header {
		text-align: center;
		margin-bottom: 4.8rem;
		&__name {
			font-size: 2rem;
			color: map-get($color-primary, dark-1);
			font-weight: map-get($font-weight, xl);
			text-transform: uppercase;
			margin-bottom: 3.2rem;
		}
		&__price {
			font-size: 6.2rem;
			font-weight: map-get($font-weight, xl);
			color: map-get($color-grey, dark-1);
			margin-bottom: 1.6rem;
		}
		&__text {
			font-size: 1.6rem;
			line-height: map-get($line-height, main);
			color: map-get($color-grey, light-1);
		}
	}

	&__sing-up {
		text-align: center;
		margin-top: 4.8rem;
	}
}
