.sec-pricing {
	&__pricing-plans {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		column-gap: 6.4rem;

		.pricing-plan {
			&--starter {
				justify-self: end;
			}
			&--complete {
				justify-self: start;
			}

			@include rwd-df(sm) {
				&--starter,
				&--complete {
					justify-self: center;
				}
			}
		}

		@include rwd-df(sm) {
			grid-template-columns: 1fr;
			row-gap: 6.4rem;
		}
	}

	&__details {
		font-size: 1.6rem;
		line-height: map-get($line-height, main);
		text-align: center;
		margin-top: 6.4rem;
		margin-bottom: 9.6rem;
	}

	&__features {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 4.8rem;

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
			row-gap: 6.4rem;
		}

		@include rwd-df(xs) {
			grid-template-columns: 1fr;
		}
	}
}
