/*
**  define styling for element components with specified with classe main footer.
*/
.footer {
	padding: 12.8rem 0;
	border-top: 1px solid map-get($color-grey, light-4);
	background-color: map-get($color-grey, dark-1);

	&__content {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		column-gap: 6.4rem;
		row-gap: 9.6rem;

		@include rwd-df(lg) {
			grid-template-columns: repeat(6, 1fr);
		}

		@include rwd-df(xs) {
			grid-template-columns: repeat(2, 1fr);
			padding: 0 5rem;
		}

		// @include rwd-df(sm) {
		// 	grid-template-columns: 1fr;
		// 	row-gap: 4.8rem;
		// }
	}

	&__heading {
		font-size: 1.8rem;
		font-weight: map-get($font-weight, lg);
		margin-bottom: 4rem;
		color: map-get($color-grey, light-4);
	}

	&__nav {
		display: flex;
		flex-direction: column;
		gap: 2.4rem;
		&__item {
		}
	}

	&__link {
		&:link,
		&:visited {
			text-decoration: none;
			font-size: 1.6rem;
			// color: map-get($color-grey, light-2);
			color: map-get($color-grey, light-3);
			transition: all map-get($transition-duration, main);
		}

		&:hover,
		&:active {
			// color: map-get($color-grey, main);
			color: map-get($color-primary, main);
		}
	}

	&__logo-col {
		grid-column: span 3;
		display: flex;
		flex-direction: column;
		gap: 2rem;

		@include rwd-df(lg) {
			grid-row: 2;
		}

		@include rwd-df(xs) {
			grid-column: span 2;
			grid-row: 5;
		}

		&__logo-link {
			margin-bottom: 3.2rem;
			text-decoration: none;
			&__img {
				width: 60%;
			}
		}

		&__copyright {
			margin-top: auto;
			font-size: 1.4rem;
			line-height: map-get($line-height, main);
			// color: map-get($color-grey, light-2);
			color: map-get($color-grey, light-3);
		}
	}

	&__address-col {
		grid-column: span 3;

		@include rwd-df(lg) {
			grid-row: 2;
		}

		@include rwd-df(xs) {
			grid-column: span 2;
			grid-row: 4;
		}

		&__address {
			margin-bottom: 2.4rem;
			line-height: map-get($line-height, sm);
			color: map-get($color-grey, light-3);
		}
	}

	&__nav-col {
		grid-column: span 2;
	}
}
