.sec-cta {
	&__content {
		border-radius: map-get($border-radius, lg);
		box-shadow: map-get($box-shadow, lg);

		display: grid;
		grid-template-columns: 2fr 1fr;

		@include rwd-df(sm) {
			grid-template-columns: 1fr;
		}

		background-image: linear-gradient(
			to right bottom,
			map-get($color-primary, light-1),
			map-get($color-primary, dark-1)
		);
		overflow: hidden;
	}

	&__info {
		padding: 4.8rem 6.4rem 6.4rem 6.4rem;
		color: map-get($color-primary, dark-2);
		&__title {
			color: inherit;
			margin-bottom: 3.2rem;
		}
		&__text {
			font-size: 1.8rem;
			line-height: 1.8;
			margin-bottom: 4.8rem;
		}

		&__form {
		}
	}

	&__img-container {
		background-image: linear-gradient(to right bottom, #eb974e59, #e67d2259),
			url('../../assets/img/cta/eating.jpg');
		background-size: cover;
		background-position: center;
		background-attachment: scroll;

		@include rwd-df(sm) {
			grid-row: 1;
			min-height: 50rem;
		}

		@include rwd-df(xs) {
			min-height: 40rem;
		}
	}
}
