.btn {
	&,
	&:link,
	&:visited {
		display: inline-block;
		text-decoration: none;
		font-size: 2rem;
		font-weight: map-get($font-weight, xl);
		padding: 1.6rem 3.2rem;
		@include rwd-df(xs) {
			padding: 1.2rem 1.6rem;
			font-size: 1.6rem;
		}

		border-radius: map-get($border-radius, main);
		// text-transform: uppercase;
		transition: all map-get($transition-duration, main);
	}

	&--outline {
		&:link,
		&:visited {
			background-color: $color-white;
			color: map-get($color-grey, main);
		}

		&:hover,
		&:active {
			background-color: map-get($color-primary, light-3);
			box-shadow: inset 0 0 0 3px $color-white;
		}
	}

	&--fill {
		&:link,
		&:visited {
			background-color: map-get($color-primary, main);
			color: $color-white;
		}

		&:hover,
		&:active {
			background-color: map-get($color-primary, dark-1);
		}
	}
}
