.sec-features {
	padding: 4.8rem 0;

	&__title {
		font-size: 1.4rem;
		text-transform: uppercase;
		letter-spacing: map-get($letter-spacing, lg);
		font-weight: map-get($font-weight, lg);
		text-align: center;
		margin-bottom: 2.4rem;
		color: map-get($color-grey, light-3);
	}

	&__logos {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		gap: 1.5rem;

		@include rwd-df(sm) {
			justify-content: center;
		}

		&__image {
			height: 2.4rem;
			filter: brightness(0);
			opacity: 50%;
		}
	}
}
