.sign-up__form {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 3rem;

	@include rwd-df(md) {
		grid-template-columns: 1fr;
	}

	&__control {
		&__label {
			display: block;
			font-size: 1.6rem;
			font-weight: map-get($font-weight, lg);
			margin-bottom: 1.2rem;
		}
		&__input {
			width: 100%;
			padding: 1.2rem;
			font-size: 1.8rem;
			background-color: map-get($color-primary, light-3);
			border-radius: map-get($border-radius, main);
			box-shadow: map-get($box-shadow, sm);
		}
	}
	&__btn {
		background-color: map-get($color-primary, dark-2);
		color: map-get($color-primary, light-3);
		align-self: end;
		padding: 1.2rem;

		&:hover {
			background-color: $color-white;
			color: map-get($color-grey, main);
		}
	}
}
