.sec-testimonials {
	.testimonials {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 3.2rem;
		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
			row-gap: 4.8rem;
		}

		@include rwd-df(xs) {
			grid-template-columns: repeat(1, 1fr);
		}
		&__item {
			&__img {
				@include squareSize(6.4rem);
				border-radius: map-get($border-radius, circle);
				margin-bottom: 1.2rem;
				// border: 3px solid map-get($color-primary, main);
				box-shadow: 0 0 5px 4px rgba(map-get($color-primary, dark-1), 0.5);
			}
			&__text {
				font-size: 1.8rem;
				line-height: map-get($line-height, lg);
				margin-bottom: 1.6rem;
			}
			&__author {
				font-size: 1.6rem;
				color: map-get($color-grey, light-1);
				font-weight: map-get($font-weight, lg);
			}
		}
	}
}
