.feature {
	&__icon {
		fill: map-get($color-primary, main);
		@include squareSize(7rem);
		background-color: map-get($color-primary, light-3);
		padding: 1.6rem;
		border-radius: map-get($border-radius, circle);
		margin-bottom: 3.2rem;
	}

	&__title {
		font-size: 2.4rem;
		color: map-get($color-grey, dark-1);
		font-weight: map-get($font-weight, xxl);
		margin-bottom: 1.6rem;
	}

	&__text {
		font-size: 1.8rem;
		line-height: map-get($line-height, lg);
	}
}
