.sec-meals {
	&__content {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		justify-items: center;
		column-gap: 6.4rem;
		row-gap: 9.6rem;

		@include rwd-df(lg) {
			column-gap: 4.8rem;
		}

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
			row-gap: 4.8rem;
		}

		@include rwd-df(xs) {
			grid-template-columns: 1fr;
			row-gap: 6.4rem;

			& > div {
				width: 75%;
				margin: 0 auto;
			}
		}
	}

	.diets {
		@include rwd-df(md) {
			grid-column: span 2;
		}

		@include rwd-df(xs) {
			grid-column: span 1;
		}
	}

	.sec-meals__link-container {
		margin-top: 4.3rem;
		text-align: center;
		font-size: 1.8rem;
	}
}
